<template>
  <div>
      <div class="mhead">
        <ul>
          <li>
            <text>自助缴费机</text>
          </li>
        </ul>
      </div>
      <div class="body" style="background: #f0f0f0;padding: 20%;margin: 0 auto">
        <div>
          <h5>请插好燃气卡,等待自动读卡</h5>
          <img src="../image/readCard.gif">
        </div>
      </div>
      <div class="foot" @click="back('tomain')">
        <text>返回</text>
      </div>
  </div>
</template>

<script>
import  AndroidPlugin from "../../Plugin/AndroidPlugin"
import Vue from 'vue'
export default {
  title: '读卡',
  name: "cardMain",
  data () {
    return {
      hasCardCount: 0,
      readCardTime: null,
      isAndroid: false,
      cardInfo:{}
    }
  },
  methods: {
    back(param) {
      this.$back(param)
    },
    // 开始判断卡
    card() {
      this.readCardTime = window.setInterval(() => {
        this.hasCardCount ++
        this.hasCard();
      },1000);
    },
    // 判断是否有卡
    hasCard() {
      let result = AndroidPlugin.hasCard(this.hasCardCount)
      console.log('第', this.hasCardCount, '遍判卡，结果是', result)
      if (result || this.hasCardCount === 5) {
        window.clearInterval(this.readCardTime)
        if (result) {
          this.readCard()
        }else {
          this.$showMessage('未检测到卡,请检查卡槽是否有卡或异物！')
        }
      }
    },
    // 读卡
    async readCard() {
      console.log('判卡成功')
      let cardResult = await AndroidPlugin.read();
      if (cardResult.code === 200) {
        this.cardInfo = cardResult.msg
        this.getUserInfo()
      }else if (cardResult.code === 500) {
        this.$showMessage('请检查卡是否插好，异常信息', cardResult.msg)
        this.back()
      }else {
        this.$showMessage('调用读卡器失败,请联系管理员！')
        this.back()
      }
    },
    // 根据卡号获取用户信息和收费记录
    getUserInfo () {
      let condition = `f_card_id = '${this.cardInfo.CardID}' and f_alias like '%${this.cardInfo.Factory}%'`
      if (this.$PosUtil.posParam.filiale.hasFiliale) {
        condition = condition + this.$PosUtil.posParam.filiale.condition
      }
      this.$resetpost(Vue.ProxyUrl + '/rs/logic/'+this.$PosUtil.posParam.revenue+'getUserfile', {data: {condition: condition}}, {
        resolveMsg: null,
        rejectMsg: null
      }).then((ret) => {
        if (ret.data.userInfo.f_table_state !== '正常') {
          this.back('该表状态为"' + ret.data.userInfo.f_table_state + '"不能购气,请联系工作人员')
        } else {
          // 该用户为正常可用状态
          console.log('跳转页面')
          this.$goto('user-info', {userInfo:ret.data.userInfo,stairPrices:ret.data.sells,cardInfo:this.cardInfo}, 'self', this.back())
        }
      }).catch((error) => {
        if (error.status === 404) {
          this.back('连接后台服务失败,请检查网络连接或联系工作人员！')
        } else if (error.status === 500){
          this.back('请确认是否为正确的燃气卡！' + error.data)
        } else {
          this.back('查询不到该档案,请联系工作人员！')
        }
      })
    }
  },
  ready () {
    // 调用定时器开始判卡
    this.card()
  }
}
</script>
<style>
</style>



